 #books {
     position: absolute;
 }

 #books:before,
 #books:after {
     z-index: -1;
     position: absolute;
     content: "";
     bottom: 15px;
     left: 105px;
     width: 50%;
     top: 80%;
     max-width: 300px;
     background: rgb(10, 1, 1);
     -webkit-box-shadow: 0 15px 10px rgb(7, 0, 0);
     -moz-box-shadow: 0 15px 10px rgb(10, 1, 1);
     box-shadow: 0 15px 10px rgb(10, 1, 1);
     -webkit-transform: rotate(-15deg);
     -moz-transform: rotate(-15deg);
     -o-transform: rotate(-5deg);
     -ms-transform: rotate(-5deg);
     transform: rotate(-5deg);
 }

 #books:after {
     -webkit-transform: rotate(5deg);
     -moz-transform: rotate(5deg);
     -o-transform: rotate(5deg);
     -ms-transform: rotate(5deg);
     transform: rotate(5deg);
     right: 10px;
     left: auto;
 }

